.footer {
  margin: 0 auto;
  background-color: #f2f2f2;
}

@media (min-width: 1440px) {
  .footer__contacts {
    margin-top: 86px;
  }
}

.footer__logo-link-image {
  display: block;
  margin: 40px auto;
}

